.about-content-container {
    align-items: center;
    display: flex;
    justify-content: center;
    margin: 0 40px;
    max-width: 1250px;
    width: 100%;
}

.about-intro-container {
    margin: 80px 0 100px 0;
}

.about-intro-picture img{
    width: 500px;
}

.about-intro-text {
    max-width: 500px;
}

.about-intro-text h2 {
    margin-bottom: 20px;
}

.about-intro-wrapper {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.about-text-container {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

#about-title {
    background-image: url(components/images/about.png);
}

.about-wrapper {
    margin: 0 auto;
    max-width: 1150px;
    width: 90%;
}

.about-wrapper-2{
    margin: 0 auto;
    max-width: 950px;
    width: 90%;
}

.meet-practicioners {
    align-items: center;
    background-color: black;
    display: flex;
    height: 100px;
    justify-content: center;
    width: 100%;
}

.meet-practicioners h1 {
    color: white;
    font-size: 32px;
}

.practicioner-container {
    background-color: rgb(44, 44, 44);
}

.practicioner-text {
    margin: 0 100px;
    width: 400px;
}

.practicioner-text h2 {
    margin-bottom: 20px;
    text-decoration: underline;
}

.practicioner-text p {
    font-size: 18px;
}

.practicioner-wrapper {
    color: white;
    display: flex;
    justify-content: center;
    padding: 70px;
}

.resume-column {
    width: 350px;
}

.resume-column h3 {
    font-size: 18px;
    margin-bottom: 10px;
}

.resume-column p {
    margin-bottom: 20px;
}

.resume-column-container {
    width: 600px;
}

.resume-wrapper {
    display: flex;
    /* justify-content: center; */
    margin: 40px 0;
}